<template>
  <div class="myBd">
    <van-nav-bar
      title="我的BD"
      @click-left="$toPage(-1)"
      >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
    </van-nav-bar>
    <van-cell-group>
      <van-field
        v-model="name"
        label="商务经理"
        clearable
        readonly
        size="large"
      />
    </van-cell-group>
    <van-cell-group>
      <van-field
        v-model="tel"
        type="tel"
        label="联系电话"
        readonly
        clearable
        size="large"
        @click="callTel"
      />
    </van-cell-group>
    <van-popup v-model="flag">
      <div @click="callPhone">呼叫我的商务经理</div>
    </van-popup>
  </div>
</template>
<script>
// import { METHODS } from 'http';
export default {
  data() {
    return {
      name: "",
      tel: "",
      flag: false
    };
  },
  computed: {

  },
  mounted() {
    this.getMyBd();
  },
  methods: {
    getMyBd() {
      JNB.query({
        serverName: "userServer",
        cls: "BackGroundProxyMoudule",
        method: "queryStoreBd",
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            // 更新用户登录状态
            if (result.data) {
              this.name = result.data.urealname;
              this.tel = result.data.uphone;
            } else {
              this.$toast.fail("请绑定对应的商务经理");
            }
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    },
    callTel() {
      if (this.tel) {
        this.flag = !this.flag;
      }
    },
    callPhone() {
      this.callTel();
      JNB._requestNative("callPhone", this.tel);
    }
  }
};
</script>
<style scoped>
.myBd .van-popup {
  padding: 30px;
  width: 500px;
  border-radius: 10px;
}
</style>